import React, { useState, useEffect } from "react"
import { 
	Modal, 
	Button, 
	List,
	Input,
	Form,
	Switch } from 'antd'
import styled from '@emotion/styled'
import {
	PlusOutlined
} from '@ant-design/icons'

import Url from './../../../url/url.js'
import { swallErr, swallGood } from './../../../alert/alert.js'

const ModalRoleCreation = ({ dataRole, setDataRole, setSkeletonShow }) => {

	const axios = require('axios'); // AJAX

	const [isModalVisible, setIsModalVisible] = useState(false);
	const [ValueName, setValueName] = useState('')

	let actionPermissionsForm = {},
		dataPermissionsForm = {},
		nameRole = ValueName

	const showModal = () => {
	    setIsModalVisible(true);
	    setValueName('')
	};

	const handleOk = () => {

		if (Object.keys({...actionPermissionsForm, ...dataPermissionsForm}).length && nameRole) {

			setSkeletonShow(true)

			axios({
				method: 'post',
				url: `${Url}/AdminPanel/Roles`,
				headers: {
					'Content-Type': 'application/json;charset=utf-8'
				},
				data: {
					name: nameRole,
					actionPermissions: Object.values(actionPermissionsForm),
					dataPermissions: Object.values(dataPermissionsForm)
				}
			})
			.then(res => {
				let {data} = res
				let box = [...dataRole, data]

				swallGood('Готово', `Роль "${data.name}" создана`)
				setDataRole(box)
				setSkeletonShow(false)
			})
			.catch(error => {
				if (error.response) {
					setSkeletonShow(false)
				}
			})

			setIsModalVisible(false)
		} else {
			swallErr('Ой...', 'Придумайте название и выбирите хотябы одно действие')
			setValueName(nameRole)
		}
	};

	const handleCancel = () => {
	    setIsModalVisible(false);
	}

	let actionPermissions = [
		['Создать группу', 0],
		['Изменить группу', 1],
		['Удалить группу', 2],
		['Получить список групп', 3],
		['Просмотреть страницу группы', 4],
		['Установить расписание группы', 5],
		['Обновить расписание учеников', 6],
		['Добавить учеников в группу', 7],
		['Удалить ученика из группы', 8],
		['Добавить учителя', 9],
		['Создать ученика', 10],
		['Изменить ученика', 11],
		['Удалить ученика', 12],
		['Получить список учеников', 13],
		['Зайти на страницу ученика', 14],
		['Импортировать файл', 15],
		['Создать платеж', 16],
		['Создать заметку', 17],
		['Удалить заметку', 18],
		['Использовать поиск', 19]
	].map(actionData => (
		<List.Item key={actionData[1]}>
		    {actionData[0]}
		    <Switch
		    	onChange={checked => {
		    		if (checked) {
		    			actionPermissionsForm[actionData[0]] = actionData[1]
		    		} else {
		    			delete actionPermissionsForm[actionData[0]]
		    		}
		    	}} 
		    	style={{ position: "absolute", right: "15px" }} />
		</List.Item>
	))

	let dataPermissions = [
		['Телефон', '+phone'],
		['Родительский телефон', '+parentPhone'],
		['Родительские инициалы', '+parentNSM'],
		['Баланс', '+balance'],
		['Discord', '+discord'],
		['Историю платежей', '+paymentHistory'],
		['Заметки', '+notes'],
		['Историю групп', '+groupsHistory'],
		['Список групп', '+groups'],
		['Учителей', '+tutors'],
		['Расписание', '+localSchedule'],
		['Имя группы', '+GROUP_NAME'],
		['Уровень группы', '+LEVEL'],
		['Учителей группы', '+TUTOR'],
		['Учеников группы', '+PUPILS'],
		['Количество мест в группе', '+PLACES'],
		['Расписание группы', '+GLOBAL_SCHEDULE']
	].map(SeeData => (
		<List.Item key={SeeData[1]}>
		    {SeeData[0]}
		    <Switch
		    	onChange={checked => {
		    		if (checked) {
		    			dataPermissionsForm[SeeData[0]] = SeeData[1]
		    		} else {
		    			delete dataPermissionsForm[SeeData[0]]
		    		}
		    	}} 
		    	style={{ position: "absolute", right: "15px" }} />
		</List.Item>
	))

	const CreationRole = styled.div({
		display: 'inline',
		'& > button': {
			marginLeft: '10px',
			fontSize: '13px',
			height: '30px'
		}
	})

	return (
		<CreationRole>
		    <Button type="primary" onClick={showModal}>
		        Создать роль
		    </Button>
		    <Modal
		    	width={1000}
		        title="Создание роли"
		        visible={isModalVisible}
		        onOk={handleOk}
		        onCancel={handleCancel}
		    >
		        <List

				    size="small"
				    bordered={false}
				>
				    <List.Item>
				        Название роли: <Input onChange={(e) => {nameRole = e.target.value}} defaultValue={ValueName}/>
				    </List.Item>
				    <div style={{display: 'flex'}}>
				    	<div className="boxLeft col-6">
						    <List.Item style={{ fontSize: "16px", fontWeight: "500" }}>
						    	Разрешённые действия:
						    </List.Item>
						    {actionPermissions}
					    </div>
					    <div className="boxRights col-6">
					    	<List.Item style={{ fontSize: "16px", fontWeight: "500" }}>
						    	Можно видеть:
						    </List.Item>
						    {dataPermissions}
					    </div>
					    </div>
				</List>
		    </Modal>
		</CreationRole>
	)
}

export default ModalRoleCreation